.container-search-and-select-box {
    height: 130px;
    background-color: #E5E5E5;
    padding-top: 40px;
    padding-bottom: 21px;
    padding-left: 35px;
    padding-right: 35px;
    width: 100%;
}

.search-and-select-box {
    width: 100%;
    height: 55px;
    background-color: white;
    display: flex;
    /* flexbox로 설정 */
    align-items: center;
    /* 수직 중앙 정렬 */
    justify-content: start;
    /* 수평 시작점에서 요소들을 정렬 (선택사항) */

}

.container-search-and-select-box input[type="text"] {
    width: 50%;
    height: 55px;
    border: none;
    background-color: white;
    padding-left: 40px;
    /* 아이콘 영역을 위한 여백 */
    background-image: url(/static/media/searchIcon.9afd88f8.svg);
    /* 아이콘 경로를 정확하게 입력해주세요 */
    background-position: 10px center;
    /* 아이콘 위치 조정 */
    background-repeat: no-repeat;
}

.container-search-and-select-box input[type="text"]::-webkit-input-placeholder {
    width: 50%;
    color: #a9a9a9;
    /* placeholder 색상 */
}

.container-search-and-select-box input[type="text"]::placeholder {
    width: 50%;
    color: #a9a9a9;
    /* placeholder 색상 */
}

.separator {
    height: 25px;
    width: 1px;
    /* 선의 너비. 필요에 따라 조절 가능 */
    background-color: #a9a9a9;
    /* 선의 색상. 원하는 색상으로 변경 */
    vertical-align: middle;
    /* 수직 중앙 정렬 */
}

.container-search-and-select-box select {
    width: 750px;
    height: 55px;
    border: none;
    border-radius: 0;
    background-color: white;
    appearance: none;
    /* 브라우저 기본 스타일 제거 */
    -webkit-appearance: none;
    /* 크롬, 사파리에서의 브라우저 기본 스타일 제거 */
    padding-left: 40px;
    /* 아이콘 영역을 위한 여백 */
    background-image: url(/static/media/locationIcon.51d2c06e.svg), url(/static/media/selector.b9d84dee.svg);
    background-position: 10px center, right 10px center;
    background-repeat: no-repeat, no-repeat;
    color: #a9a9a9;
}

/* select 박스 확장 화살표 제거 (크롬, IE 등 대부분의 브라우저) */
.container-search-and-select-box select::-ms-expand {
    display: none;
}

