/* 기본 네브바 스타일 */
.sidebar {
  width: 250px;
  height: 100vh;
  background-color: #2e2d2f;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  padding: 50px;
  z-index: 500;
}

/* 네브바 링크 스타일 */
.nav-links {
  list-style: none;
  padding: 0;
}

.nav-item {
  text-decoration: none;
  color: #fff;
  font-size: 18px;
  padding: 15px 0;
  display: block;
}

.nav-item:hover {
  background-color: #474747;
  border-radius: 5px;
  padding-left: 10px;
  transition: 0.3s;
}

.admin-nav-item {
  text-decoration: none;
  color: #fff;
  font-size: 16px;
  padding: 15px 0;
  display: block;
}

/* 햄버거 메뉴 아이콘 초기 상태: 큰 화면에서는 숨기기 */
.hamburger-menu {
  display: none;
}

/* 작은 화면에서 네브바 숨기기, 햄버거 메뉴 보이기 */
@media (max-width: 800px) {
  .sidebar {
      display: none;
  }
  
  /* 햄버거 메뉴 보이기 */
  .hamburger-menu {
      display: block;
      position: fixed;
      top: 50px;
      left: 20px;
      font-size: 24px;
      color: #000000;
      cursor: pointer;
      z-index: 1100;
  }
  
  /* 토글된 상태에서는 사이드바 보이기 */
  .sidebar.is-visible {
      display: flex;
  }
}

.my-project {
  padding: 20px;
  background-color: #f9f9f9;
  margin-left: 250px;
  padding-top: 70px;
  max-height: calc(100vh - 70px); /* 헤더 크기를 제외한 높이 제한 */
  overflow-y: auto;
}

.myproject-project-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.myproject-project-header button {
  padding: 5px 25px;
  border: 1px solid #ccc;
  align-items: flex-end;
  font-weight: bold;
  border-radius: 4px;
  background-color: #dddddd;
  font-size: 14px;
  cursor: pointer;
}

.my-project-filters {
  display: flex;
  justify-content: flex-end;
  font-weight: bold;
  margin-bottom: 20px;
  grid-gap: 10px;
  gap: 10px;
}

.my-project-filters select {
  padding: 5px 25px;
  border: 1px solid #ccc;
  align-items: flex-end;
  font-weight: bold;
  border-radius: 4px;
  background-color: #dddddd;
  font-size: 14px;
  cursor: pointer;
}

.my-project-filters select:focus {
  outline: none;
  border-color: #007bff;
}

.myproject-task-table-header,
.myproject-task-row {
  display: grid;
  grid-template-columns: 1fr 1fr 1.5fr 1.5fr 1.5fr 1.5fr 0.5fr 0.5fr 0.5fr 1.5fr;
  grid-gap: 0;
  gap: 0;
  padding: 10px;
  border-top: 1px solid #e4e4e4;
  border-bottom: 1px solid #e4e4e4;
}



.myproject-task-table-header div,
.myproject-task-row div {
  border-right: 1px solid #ddd;
  padding: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.myproject-task-table-header div:last-child,
.myproject-task-row div:last-child {
  border-right: none;
}

.myproject-task-section {
  margin-bottom: 40px;
}

.myproject-task-section h3 {
  font-size: 18px;
  margin-top: 20px;
  margin-bottom: 10px;
  color: #333;
}

