/* 네비게이션 스타일 */
.react-calendar__navigation {
  width: 100%;
  height: 3.5rem;
  display: flex;
  justify-content: space-between; /* 버튼 정렬 */
  text-align: center;
  align-items: center;
  padding: 0 1rem; /* 좌우 간격 조정 */
}

.react-calendar__navigation__label {
  font-size: 16px;
  font-weight: bold;
  background-color: white;
  border: none;
}

.react-calendar__navigation__prev-button,
.react-calendar__navigation__next-button {
  border: none;
  background-color: white;
  font-size: 1.2rem;
  cursor: pointer;
  color: #333; /* 버튼 색상 */
  opacity: 1; /* 버튼 보이게 수정 */
}

/* 날짜 스타일 */
.react-calendar__tile {
  text-align: center;
  padding: 10px;
  background-color: white;
  border: none;
  font-size: 14px;
}

.react-calendar__month-view__days__day--weekend {
  color: red; /* 일요일 색상 */
}

.react-calendar__month-view__days__day:nth-child(7n-1) {
  color: blue; /* 토요일 색상 */
}

/* Hover 및 Active 스타일 */
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background-color: #f0f8ff;
  border-radius: 50%;
}

.react-calendar__tile--active {
  background-color: #b3d7ff;
  color: white;
  border-radius: 50%;
}

.department-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  min-width: 300px;
}

.modal-content h3 {
  margin-top: 0;
  margin-bottom: 15px;
}

.modal-content select {
  width: 100%;
  padding: 8px;
  margin-bottom: 15px;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  grid-gap: 10px;
  gap: 10px;
}

.modal-buttons button {
  padding: 8px 16px;
  border-radius: 4px;
  border: none;
  cursor: pointer;
}

.modal-buttons button:first-child {
  background-color: #007bff;
  color: white;
}

.modal-buttons button:first-child:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.modal-buttons button:last-child {
  background-color: #6c757d;
  color: white;
}

